.skills {
  padding: 40px;
  margin: 30px;
  /* background-image: linear-gradient(-45deg,rgb(15, 179, 40), rgb(135, 235, 217)); */
  background-size: 200%;
  scroll-margin-top: 60px;
}

.skills p {
  font-size: 30px;
  /* color: white; */
  color: white;
  text-align: center;
  font-weight: bold;
}
.skills p:hover{
  transition: 0.8s;
  color: gray;
}

.tools {
  width: 500px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
  margin: 30px auto;
}

.tools .fab {
  font-size: 40px;
}
