@media only screen and (max-width: 600px) {
  .skills {
    margin: 20px;
    padding: 40px 0;
  }
  .tools {
    gap: 15px;
    width: 300px;
  }
  
}

@media only screen and (max-width: 300px) {
  .tools {
    width: 200px;
  }
  .details img {
    width: 100px;
  }
  .info p {
    font-size: 24px;
  }
}
