.about {
  /* background-color: white; */
  width: 100%;
  margin-bottom: 20px;
  /* border-radius: 10px; */
  padding: 4% 8%;
  display: flex;
  justify-content: space-between;
  position: relative;
  height: 95vh;
  box-sizing: border-box;

}
.fog{
  position: absolute;
  height: 200vh;
  width: 100%;
  /* background-color: tomato; */
  left: 0;
  top: 0;
  z-index: -1;
}
.fog img{
  height: 100%;
  width: 100%;
}
.info p {
   color: whitesmoke;
}
.image {
  /* height: max-content; */
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* border: 2px solid red; */

  padding: 10px;
  /* border-radius: 50%; */
  /* background: linear-gradient(-45deg,  #ee7752,rgb(255, 255, 200), #23a6d5, #23d5ab); */
  /* background: linear-gradient(-45deg, #f7f4f3, #aeaeae, #4f4f4f, #2f2f2f); */
  box-sizing: border-box;
  background-size: 400% 400%;
  position: relative;
  animation: change 10s ease-in-out infinite;
}
.image img {
  height: 95%;
  width: 100%;
 
  /* filter: grayscale(); */
  object-fit: cover;
}


.white {
  height: 400px;
  width: 340px;
  border-radius: 50%;
  /* background-color: white; */
  /* border: 3px solid white ; */
}
.gifimg {
   width: 50px;
   height: 50px;
}

@keyframes change {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

.info {
  width: 50%;
  /* border: 2px solid red; */
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;
}

.info button{
  padding: 15px;
    width: 250px;
    font-size: 20px;
    /* background-image: linear-gradient(-45deg,blue, skyblue); */
    background-image: linear-gradient(-45deg, black, white);
    background-size: 200%;
    font-weight: bold;
    border: none;
    box-shadow: 2px 2px 4px rgba(0, 0, 0,0.3);
    margin: 20px;
    font-family: sans-serif;
    /* color: white; */
    color: black;
    cursor: pointer;
}

.info button:hover{
  background-image: linear-gradient(-45deg,whitesmoke,black);
  color:white;
  transition:0.3s;
  border-radius: 1rem;
}

.info button a{
 
    color: white;
}

.about p {
  margin: 20px 0;
  font-size: 40px;
  font-weight: 400;
  font-family: 'Sacramento', cursive;
}
.about h1 {
  color: rgb(56, 56, 56);
  font-size: 30px;
}
.typical {
  color: blue;
}

@media only screen and (max-width: 600px) {
  .about {
    padding: 20px;
    flex-direction: column-reverse;
  }
  .about {
     padding-top: 30px;
  }
  .image{
    width: 100%;
  }
  .white {
    height: 400px;
    width: 240px;
    /* background-color: white; */
    /* border: 3px solid white ; */
    margin-top: 300px;
  }
  .info p{
    font-size: 30px;
  }
 
  .info {
    width: 100%;
    margin-top: 200px;
  }
  .gifimg {
     width: 50px;
     height: 50px;
     margin-top: 180px;
  }
 
}

